import { useState, useEffect } from "react";
import "./App.css";
import Button from "react-bootstrap/Button";
import { IoIosArrowBack } from "react-icons/io";
import { Form, InputGroup } from "react-bootstrap";
import axios from "axios";

import LABEL from "./const/label";
import API from "./const/api";

function App() {
  const [isValid, setIsValid] = useState({
    quantity: false,
    type: false,
    available: false,
  });
  const [username, setUsername] = useState("");

  const handleChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);

    // Reset status valid if length is zero
    if (username.length === 0) {
      return setIsValid({
        quantity: false,
        type: false,
        available: false,
      });
    }

    if (username.length < 2 || username.length > 20) {
      setIsValid((prevIsValid) => ({
        ...prevIsValid,
        quantity: false,
        available: false,
      }));
      return;
    }

    const usernameRegex = /^[a-zA-Z0-9_]*$/;
    if (!usernameRegex.test(username)) {
      setIsValid((prevIsValid) => ({
        ...prevIsValid,
        type: false,
        quantity: !(username.length < 2 || username.length > 20),
        available: false,
      }));
      return;
    }

    setIsValid((prevIsValid) => ({
      ...prevIsValid,
      quantity: true,
      type: true,
    }));
  };

  useEffect(() => {
    // Debounce to check availability of username after 1s to avoid spamming the server
    const checkDebounceFn = setTimeout(() => {
      // Check availability of username only when pass 2 rules
      if (username && isValid.quantity && isValid.type) {
        axios
          .post(API.checkUsernameAvailability, {
            action: "checkFieldAvailability",
            email: username,
            hint: "handle",
            language: "en",
          })
          .then(function (response) {
            if (response.status === 200) {
              setIsValid((prevIsValid) => ({
                ...prevIsValid,
                available: true,
              }));
            } else {
              setIsValid((prevIsValid) => ({
                ...prevIsValid,
                available: false,
              }));
            }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    }, 500); // 500ms is debounce

    return () => {
      clearTimeout(checkDebounceFn);
    };
  }, [username, isValid.quantity, isValid.type]);

  return (
    <div className="App">
      <div className="form-wrapper">
        <div className="form-inner">
          <header className="App-header">
            <p className="title">
              <IoIosArrowBack color="gray" /> {LABEL.Create_User_Name_Title}
            </p>
          </header>

          <p className="hint-text">{LABEL.Hint_User_Name_Title}</p>
          <p className="handle-text">{LABEL.Handle_Title}</p>
          <InputGroup className="mb-3">
            <InputGroup.Text id="basic-addon1">@</InputGroup.Text>
            <Form.Control
              placeholder={LABEL.User_Name_Input_Placeholder}
              aria-label="Username"
              aria-describedby="basic-addon1"
              value={username}
              onChange={(e) => handleChangeUsername(e)}
            />
          </InputGroup>
          <div className="rule-container">
            <p
              id="ruleQuantity"
              className={
                "rule-text " + (isValid.quantity ? "rule-text-valid" : "")
              }
            >
              {LABEL.Rule_User_Name_Length_Label}
            </p>
            <p
              id="ruleType"
              className={"rule-text " + (isValid.type ? "rule-text-valid" : "")}
            >
              {LABEL.Rule_User_Name_Type_Label}
            </p>
            <p
              id="ruleAvailable"
              className={
                "rule-text " + (isValid.available ? "rule-text-valid" : "")
              }
            >
              {LABEL.Rule_User_Name_Available_Label}
            </p>
          </div>
          <Button
            disabled={!(isValid.quantity && isValid.type && isValid.available)}
            id="nextBtn"
            className={
              "next-btn " +
              (isValid.quantity && isValid.type && isValid.available
                ? "active-btn"
                : "disable-btn")
            }
            variant="info"
            onClick={() => alert("Next button clicked")}
          >
            {LABEL.Next_Btn_Text}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default App;
